@import url(https://fonts.googleapis.com/css2?family=Inter:wght@400;500;600&display=swap);
body,h1,h2,h3,h4,p,ul[class],ol[class],li,figure,figcaption,blockquote,dl,dd{margin:0}.main{display:flex;height:100vh;background:#ffffff;font-family:Inter;font-style:normal}.sidebar{width:191px;padding:34px 10px 23px 20px;line-height:1.3;font-weight:500;font-size:12px;color:#4D586D;display:flex;flex-direction:column;justify-content:space-between}.sidebar .logo{margin-bottom:28px;margin-left:2px}.sidebar .menu_item{display:flex;margin-bottom:30px;align-items:center}.sidebar .menu_item .icon{width:20px;margin-right:20px}.sidebar .menu_item.active .menu_name{color:#1E2A3B;font-weight:600}.sidebar .menu_item:hover .menu_name{color:#2361FF}.sidebar .menu_item .menu_name{cursor:pointer}.sidebar .menu_item .menu_badge{color:#8A95AB;font-weight:500;font-size:11px;background:#F2F6FF;border-radius:5px;padding:4px 7px 2px;line-height:13px;margin-left:8px}.sidebar .menu_item .menu_badge.quantity{font-weight:600;margin-left:13px;padding:3px 3px 1px 3px}.sidebar .menu_item .menu_badge.empty{padding-top:0;padding-bottom:0}.sidebar .human{display:flex}.sidebar .human div{font-style:normal;font-weight:600;font-size:12px;line-height:15px;color:#1E2A3B;margin-left:15px}.content_wrapper{background:#E5E5E5;padding:15px;width:100%}.content_wrapper .content{background:#ffffff;width:100%;height:100vh;box-shadow:5px 7px 12px rgba(233,236,245,0.53);border-radius:7px;display:flex;justify-content:center;align-items:center;flex-direction:column}.content_wrapper .content .line1{margin-top:57px;font-weight:600;font-size:15px;line-height:18px;display:flex;align-items:center;color:#29354B}.content_wrapper .content .line2{font-weight:normal;font-size:15px;line-height:18px;display:flex;align-items:center;color:#808BA0;margin-top:15px}


